.navbar{
     display: flex;
     justify-content: space-around;
     padding: 16px;
     box-shadow: 0 1px 3px -2px black;
}
.nav-logo{
    display:flex;
    align-items: center;
    gap: 10px;
}
.nav-logo p{
    color: #171717;
    font-size: 30px;
    font-weight: 600px;
}

.nav-menu{
    display:flex ;
    align-items: center;
    list-style: none;
    gap: 50px;
    color: #626262;
    font-size: 20px;
    font-weight: 500px;
}
.nav-menu li{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 3px;
    cursor: pointer;
}
.nav-menu hr{
    border: none;
    width: 80%;
    height: 3px;
    border-radius: 10px;
    background: #FF4141;
}
.nav-login-cart {
    display: flex;
    align-items: center;
    gap: 45px;
}
.nav-login-cart button{
    width: 158px;
    height: 59px;
    outline: none;
    background:white;
    border: 1px solid #515151;
    border-radius: 70px;
    cursor: pointer;
}
.nav-login-cart button:active{
    background: #FF4141;
}
.nav-cart-count{
    display: flex;
    width: 20px;
    height: 20px;
    margin-top: -35px;
    margin-left: -55px;
    justify-content: center;
    align-items: center;
    background: #FF4141;
    border-radius: 9px;
    font-size: 14px;
    color: white;

}